@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.note-item{
    width: 20rem;
    height: 18rem;
    /* background-color: #ffc400; */
    margin: 2.5rem;
    -webkit-transform: skewY(2deg);
            transform: skewY(2deg);
    position: relative; 
    box-shadow: 10px 10px 14px 1px #626262;
    box-sizing: border-box;
}

.note-title{
    padding: 1rem;
    margin: 0;
    /* background-color: #bb8f00;  */
    color: #fff;
	font-size: 1.6rem;
    text-transform: uppercase;
}

.note-desc{
    padding: 0rem 1rem 1.5rem 1rem;
    text-align: justify;
    font-family: 'Patrick Hand', cursive;
    text-align: justify;
    white-space: pre-line;
    text-align-last: left;
    max-width: 90%;
    max-height: auto;
    overflow: hidden;
    text-overflow: revert;
    ;}

.btn-close{
    position: absolute;
    top: 1.5rem;
    right: 1rem;
    font-size: 1.5rem;
    color: rgb(41, 41, 41);
    cursor: pointer;
}

.noteHolder{
    display: flex;
    align-items: center;
    justify-content:space-evenly;
    flex-wrap: wrap;
    margin-left: 25%;
}

.textArea {
    border: none; /* Removes the default border */
    background-color: inherit;
    padding: 0rem 1rem 1.5rem 1rem;
    text-align: justify;
    font-family: 'Patrick Hand', cursive;
    display: block;
    max-width: 90%;
    max-height: auto;
    font-size: 1rem;
    resize: none;
    margin: 16px 0px;
    text-align: justify;
    white-space: pre-line;
    text-align-last: left;

  }
  
  .textArea:focus {
    outline: none /* Removes the border when the input is clicked */
  }

.fa-check{
    cursor: pointer;
    position: absolute;
    font-size: 1.6rem;
    right: 1.2rem;
    bottom: .5rem;
}

.onEdit{
    outline: 3px solid rgb(255, 0, 0);
}
.sidebar{
    position: absolute;
    left:0;
    top: 0;
    height: 100vh;
    width: 17.25%;
    background-color: #36363D;
    z-index: 1024;
    transition: .8s ease-out all;
    display: flex;
    flex-direction: column;
    color: #fff;
}

.username{
    font-size: 2.8rem;
    letter-spacing: 0.25rem;
}

.sidebar h1{
    position: relative;
    margin: 1.5rem auto;
}

.sidebar h1::after{
    content: '';
    position: absolute;
    bottom: -.5rem;
    left: 12.5%;
    width: 75%;
    border-bottom: 1.5px solid #9e9e9e;
}

.btn-create{
    color: #EAF3F0;
    font-family: inherit;
    border-radius: 0 2rem 2rem 0;
    background-color: #AEA8AB;
    padding: 0.5rem;
    font-weight: bold;
    font-size: 1.5rem;
    text-decoration: none;
}

.div{
    margin-top: 2.5rem;
}

.fa-plus-circle{
    color:#3B323C;
    position: relative;
    z-index: 1024;
}

.fa-plus-circle::after{
    content: '';
    width: 80%;
    height: 80%;
    position: absolute;
    top: 50%;
    left: 10%;
    -webkit-transform: translateX(-150%);
            transform: translateX(-150%);
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background-color: #fff;
    z-index: -1;
    border-radius: 2rem;
    transition: .8s ease-in-out;
}


h2{
    font-size: 2.4rem;
    padding: 0rem 1rem;
    margin-bottom: 0rem;
    position: relative;
}



.note-ul{
    list-style: none;
    padding-left: 0;
}

.note-li{
    font-size: 1.1rem;
    letter-spacing: .25rem;
    text-transform: uppercase;
    background-color: #AEA8AB;
    font-weight: bold;
    color: #EAF3F0;
    margin: .75rem auto;
    padding: .70rem ;
    border-radius: 1.2rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}


.btn-edit{
    cursor: pointer;
    color: #ffffff;
    font-size: 1rem;
    padding-left: .5rem;
}

.btn-edit:hover{
    color: #AEA8AB;
}

.overlay{
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(63, 63, 63, 0.609);
    z-index: 23232;
    align-items: center;
    justify-content: center;
}

.overlay.open{
    display: flex;
}

.form-container{
    width: 35rem;
    height: 30rem;
    position: relative;
    background-color: #353535ef;
    display: flex;
    justify-content:center;
    flex-direction: column;
    border-radius: 2.5rem;
}


label{
    display: block;
    max-width: 15rem;
    font-size: 1.78rem;
    font-weight: 600;
    margin-bottom: .5rem;
    color: #fff;
}

input{
    width: 12.5rem;
    border-radius: 0.3rem;
    margin-bottom: 2rem;
    height: 1.5rem;
    padding: .5rem;
}

textarea{
    padding: .5rem;
    margin: 2rem auto;
    resize: none;
}

.input-container{
    margin-left: 5.5rem;
}

.btn-submit{
    margin: 0 auto;
    font-size:1.3rem;
    font-weight: 600;
    color: rgb(0, 0, 0);
    text-decoration: none;
    padding: .75rem 1.5rem;
    background-color: #fff;
    border-radius: 1rem;
    transition: .6s ease all;
    max-width: 25rem!important;
    margin: 0 auto!important;
}

.btn-submit:hover{
    background-color:rgb(175, 175, 175);



    color: rgb(50, 50, 50);

}

.btn-close-form{
    position: absolute;
    font-size: 2rem;
    top: 1.5rem;
    right: 2rem;
    cursor: pointer;
    color: #fff;
}
